.companyHost {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0px 10px 0 30px;
}


.billingInfoHost {
  padding: 0px 10px 0 30px;

  .selectControl {
    margin-left: 0;
  }

  .menuOverlayList {
    width: 20%;
  }

  label {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: gray;
    font-weight: 500;
  }

  .react-datepicker-wrapper {
    margin-top: 5px;
    width: 100%;
  }
}

.tasksTableHost {
  tbody>tr>:nth-child(2) {
    width: 25%;
    font-family: var(--app-text-main-font);
  }

  tbody>tr>:nth-child(3) {
    width: 20%;
    font-family: var(--app-text-main-font);
  }

  tbody>tr>:nth-child(4) {
    width: 20%;
    font-family: var(--app-text-main-font);
  }

  table {
    border-collapse: separate;
    border-spacing: 0;
    width: 100%;
  }

  tr {
    background-color: white;
  }

  tr {
    height: 48px;
    font-size: 16px;
  }

  th {
    font-size: 20px;
    min-width: 200px;
  }

  table tr th,
  table tr td {
    padding: 12px;
  }

  table tr th:first-child,
  table tr td:first-child {
    /* border-left: 1px solid #bbb;*/
  }

  a {
    color: gray;
  }

  table tr th {
    font-family: var(--app-text-main-font);
    color: #FFFFFF;
    background: gray;
    text-align: left;
    font-weight: 400
  }

}

.tableHost {
  tbody>tr>:nth-child(2) {
    width: 25%;
    font-family: var(--app-text-main-font);
  }

  tbody>tr>:nth-child(3) {
    width: 20%;
    font-family: var(--app-text-main-font);
  }

  tbody>tr>:nth-child(4) {
    width: 20%;
    font-family: var(--app-text-main-font);
  }

  table {
    border-collapse: separate;
    border-spacing: 5px;
    width: 100%;
  }

  tr {
    background-color: white;
  }

  tr {
    height: 48px;
    border: 1px solid #bbb;
  }

  table tr th,
  table tr td {
    padding: 20px;
  }

  table tr th:first-child,
  table tr td:first-child {
    /* border-left: 1px solid #bbb;*/
  }

  a {
    color: gray;
  }

  table tr th {
    font-family: var(--app-text-main-font);
    color: #FFFFFF;
    background: gray;
    text-align: left;
    font-weight: 400;
    width: 300px;
    //border: 1px solid #bbb;

  }

  /* top-left border-radius */
  table tr:first-child th:first-child {
    //border-top-left-radius: 6px;

  }

  /* top-right border-radius */
  table tr:first-child th:last-child {
    //border-top-right-radius: 6px;
  }

  /* bottom-left border-radius */
  table tr:last-child td:first-child {
    //border-bottom-left-radius: 6px;
  }

  /* bottom-right border-radius */
  table tr:last-child td:last-child {
    //border-bottom-right-radius: 6px;
  }
}

.sectionTitle {
  font-family: var(--app-text-main-font);
  font-size: 24px;
  font-weight: 600;
  color: gray;
  padding-top: 10px;
}

.formWrapper {
  display: flex;
  //width: 100%;
  gap: 2.3em;
}

.toggleDayPicker {
  width: 50%;
}

.form {
  max-width: 100%;
  flex-basis: 0;
}

.inputField {
  font-family: var(--app-text-main-font);
  font-size: 16px;
  color: gray;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid var(--app-color-gray-lighter);
  width: 100%;
  outline: none;
  height: 56px;
  margin-top: 5px;
  padding: 0 10px 0 10px;
}

.noPaymentInfo {
  font-family: var(--app-text-main-font), serif;
  color: var(--app-color-gray-light_nuance2);
  font-size: 16px;
}

.containerWrapper {
  display: flex;
  gap: 24px;
  margin-top: 15px;
}

.inputWrapper {
  width: 50%;
}

.errorParagraph {
  margin: 10px 0 0 0;
  text-align: left;
  vertical-align: center;
  color: red;
  font-family: var(--app-text-main-font);
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 0.8rem;
}


.trialContainer {

  background-color: #FFFFFF;
  border-radius: 4px;

}

.trialTitle {
  margin: 0 0 8px 0px;
  font-weight: bolder;
  font-size: 16px;
  color: gray;
  text-align: left;
  font-family: var(--app-text-main-font), serif;
}

.dashboardTitle {
  margin: 0 0 8px 0px;
  font-weight: 400;
  font-size: 20px;
  color: gray;
  text-align: center;
  font-family: var(--app-text-main-font), serif;
}

.subTitle {
  margin: 0 0 8px 0px;
  //font-weight: 400;
  font-size: 14px;
  color: gray;
  text-align: left;
  font-family: var(--app-text-main-font), serif;
}

.trialText {
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 10px;
  color: black;
  font-weight: 600;
  font-size: 24px;
}

.trialButton {
  font-family: var(--app-text-main-font), serif;
  border-radius: 4px;
  //background-color: var(--app-color-gray-dark);
  color: #FFFFFF;
  font-size: 16px;
  margin-left: 3px;
  height: 32px;
}

.updateButton {
  font-family: var(--app-text-main-font), serif;
  border-radius: 4px;
  color: #FFFFFF;
  font-size: 16px;
  margin-left: 3px;
  height: 40px;
}

.plansContainer {
  margin-bottom: 24px;
  display: flex;
}

.additionalPlanContainer {
  width: 400px;
  border-radius: 8px;
  text-align: center;
  background-color: white;
}

.planTitle {
  text-align: left;
  margin-left: 35px;
  margin-bottom: 0;
  font-family: var(--app-text-main-font);
  font-size: 24px;
  color: gray;
  font-weight: 600;
}

.planDescriptionContainer {
  display: flex;
  margin-left: 35px;
  
  img {
    align-self: baseline;
    margin-top: 20px;
    margin-right: 10px;
  }
}

.planPrice {
  text-align: left;
  margin-left: 35px;
  margin-top: 10px;
  margin-bottom: 0;
  font-weight: 200;
  font-family: var(--app-text-main-font);
  font-size: 20px;
  color: gray;
}

.planDescription {
  font-family: var(--app-text-main-font), serif;
  color: gray;
  font-size: 16px;
  text-align: left;
}

.planButton {
  width: 85%;
  border: 1px solid var(--app-color-gray-dark);
  color: var(--app-color-gray-dark);
  border-radius: 4px;
  background-color: var(--app-color-white-lightest);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4px;
}

.month {
  font-family: var(--app-text-main-font), serif;
  font-size: 16px;
}

.currentInvoicesContainer {
  margin-top: 30px;
  margin-bottom: 40px;
  background-color: white;
  border: 1px solid var(--app-color-gray-lighter);
  margin-right: 24px;
  float: left;
  border-radius: 8px;
  padding: 30px;
  padding-left: 20px;
  padding-right: 20px;

}

.logoutButton {
  border: 1px solid var(--app-color-error);
  background-color: transparent;
  border-radius: 4px;
  color: var(--app-color-error);
  width: 200px;
}

.boxesHost {
  margin: 10px;
  width: 200px;

  .products {
    text-align: center;
    font-size: 24px;
    font-family: var(--app-text-main-font), serif;
    margin-left: auto;
    margin-right: auto;
    font-weight: 300;
    padding-top: 20px;
  }

  .checkoutRightContainer {
    margin-top: 50px;

    .productsContainer {
      box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
      min-height: 460px;
    }

    .servicesContainer {
      .hr {
        border-top: 1px solid #D2D2D2;
      }

      padding-bottom: 10px;
      background: var(--app-color-gray-lighter);
      border-radius: 8px;
    }
  }

  .endingMessage {
    padding: 0px 24px 24px 24px;
    font-size: 12px;

    font-family: var(--app-text-main-font), serif;

  }
}

.userPanelHost {
  display: flex;
  flex-direction: column;
  background-color: rgb(246, 244, 236);
  border-radius: 7px;
  padding: 20px 0px 20px 0px;

}

.userPanelRows {
  display: flex;
  flex-direction: column;
  background-color: rgb(246, 244, 236);
  border-radius: 7px;
  padding: 0px 20px 00px 20px;
}


.plansHost {
  background-color: rgb(246, 244, 236);
  padding: 10px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-wrap: wrap;
  align-content: flex-start;
}

.wallet {
  margin-top: 20px;
  width: 80%;
  margin-bottom: 100px;
  border-radius: 5px;
  padding: 20px 10px 20px 15px;
  background-color: rgb(46, 149, 84);
  background-color: rgb(246, 244, 236);
  height: auto;
}
