.membershipHost {
  padding-top: 10px;
  padding-bottom: 10px;
  padding: 15px 15px 15px 15px;
  align-items: center;
  text-align: center;

  font-family: var(--app-text-secondary-font);
  font-size: 30px;
  color: #0d0d0d;
  font-weight: 800;

}

.cancelDescription {
  font-family: var(--app-text-secondary-font), serif;
  color: var(--app-color-gray-dark);
  font-size: 16px;
  font-weight: 100;
  text-align: center;
  margin-top: 0;
  margin-top: 20px;
  margin-bottom: 20px;
}


.benefitContainer {
  background-color: gray;;
  border-radius: 6px;
  text-align: center;
  margin-bottom: 10px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 10px;
}

.benefitTitle {
  text-align: left;
  margin-left: 15px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 30px;
  color: var(--app-color-gray-dark);
}

.benefitDescription {
  text-align: left;
  margin-left: 15px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px;
  color: var(--app-color-gray-dark);
}

.buttonsContainer {
  display: inline-block;
}

.unsubscribeButton {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 16px 24px;
  gap: 10px;
  width: 224px;
  height: 56px;
  background-color: var(--app-color-error);
  border-radius: 2px;
  color: var(--app-color-white-lightest);
  border: 3px solid var(--app-color-error);
  margin-right: 5px;
}

.continueUnsubscribeButton {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 16px 24px;
  gap: 10px;
  width: 224px;
  height: 56px;
  background-color: #1b2282;;
  border-radius: 2px;
  color: var(--app-color-white-lightest);
  border: 3px solid #1b2282;;
  margin-right: 5px;
}
.dontContinueButton {
  display: flex;
  margin-top: 10px;  
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 16px 24px;
  gap: 10px;
  width: 100%;
  height: 56px;
  background-color: var(--app-color-white-lightest);
  border-radius: 2px;
  color: #1b2282;
  border: 3px solid #1b2282;
  margin-left: 5px;
}


.questionaireContainer {
  align-items: center;
  text-align: center;
  font-family: var(--app-text-secondary-font);
  font-size: 16px;
  font-weight: 10;
  padding: 15px 15px 15px 15px;
}

.inputContainer {
  display: block;
  margin-bottom: 10px;
}
.optionsContainer {
  display: block;
  margin-bottom: 12px;
  cursor: pointer;
  font-family: var(--app-text-secondary-font);
  font-size: 16px;
  font-weight: 10;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkbox {
  width: 20px;
  height: 20px;
}

.othersContainer {
  width: 500px;
  height: 30px;
  font-family: var(--app-text-secondary-font);
  font-size: 16px;
  font-weight: 10;
}